import React, { useState } from 'react';

// Interface pour structurer les donn√©es de recherche
interface ResearchCriteria {
    keywords: string;
    sector: string;
    location: string;
}

export default function ResearchCard() {
    // 1. Initialisation de l'√©tat pour les champs du formulaire
    const [criteria, setCriteria] = useState<ResearchCriteria>({
        keywords: '',
        sector: '',
        location: '',
    });

    // 2. √âtat pour stocker le nombre de r√©sultats trouv√©s (simul√©)
    const [resultsCount, setResultsCount] = useState<number | null>(null);
    
    // 2bis. √âtat pour g√©rer l'√©tat de chargement
    const [isLoading, setIsLoading] = useState(false);

    // 3. Fonction g√©n√©rique pour g√©rer les changements d'input
    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setCriteria(prevCriteria => ({
            ...prevCriteria,
            [name]: value,
        }));
    };

    // 4. Fonction pour g√©rer la soumission du formulaire
    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault(); // Emp√™che le rechargement de la page par d√©faut
        
        setIsLoading(true); // Active le chargement

        console.log('Recherche soumise avec les crit√®res :', criteria);

        // --- SIMULATION D'APPEL API ---
        
        // Simuler les r√©sultats apr√®s un d√©lai (remplacez ceci par votre logique API)
        setTimeout(() => {
            const simulatedCount = Math.floor(Math.random() * 500) + 50; 
            setResultsCount(simulatedCount);
            setIsLoading(false); // D√©sactive le chargement
            console.log(`Recherche termin√©e. ${simulatedCount} entreprises trouv√©es.`);
        }, 1500); 
    };

    return (
        // Conteneur principal
        <div className="bg-white p-6 rounded-lg shadow-xl max-w-2xl mx-auto">
            
            {/* Formulaire de recherche */}
            <div className="mb-4">
                <h2 className="text-xl font-bold text-gray-800 mb-4">Recherche d'Entreprises</h2>
                
                {/* üö® CHANGEMENT CL√â: Utilisation de grid-cols-1 sur toutes les tailles (y compris md) */}
                <form className="grid grid-cols-1 gap-4" onSubmit={handleSubmit}>
                    
                    {/* CHAMP 1: Mots-cl√©s (Occupe toute la seule colonne) */}
                    <input 
                        type="text" 
                        name="keywords"
                        placeholder="Mots-cl√©s (ex: D√©veloppeur, IA)" 
                        value={criteria.keywords}
                        onChange={handleChange}
                        className="col-span-1 p-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                        disabled={isLoading}
                    />
                    
                    {/* CHAMP 2: Secteur (Occupe toute la seule colonne) */}
                    <input 
                        type="text" 
                        name="sector"
                        placeholder="Secteur d'activit√©" 
                        value={criteria.sector}
                        onChange={handleChange}
                        className="col-span-1 p-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                        disabled={isLoading}
                    />
                    
                    {/* CHAMP 3: Localisation (Occupe toute la seule colonne) */}
                    <input 
                        type="text" 
                        name="location"
                        placeholder="Localisation" 
                        value={criteria.location}
                        onChange={handleChange}
                        className="col-span-1 p-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                        disabled={isLoading}
                    />
                    
                    {/* BOUTON: (Occupe toute la seule colonne) */}
                    <button 
                        type="submit" 
                        className={`
                            col-span-1 p-2 rounded-md font-semibold text-white transition duration-200 
                            ${isLoading 
                                ? 'bg-gray-400 cursor-not-allowed' 
                                : 'bg-blue-600 hover:bg-blue-700'
                            }
                        `}
                        disabled={isLoading}
                    >
                        {isLoading ? (
                            <div className="flex items-center justify-center">
                                {/* Simule un spinner simple avec Tailwind */}
                                <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                </svg>
                                Chargement...
                            </div>
                        ) : (
                            'Rechercher'
                        )}
                    </button>
                    
                </form>
            </div>
            
            <hr className="my-4 border-gray-200" />
            
            {/* Affichage du nombre de r√©sultats */}
            <div className="results-display text-center">
                {resultsCount !== null ? (
                    <p className="text-lg text-gray-700">
                        üéâ **{resultsCount}** entreprises trouv√©es selon vos crit√®res.
                    </p>
                ) : (
                    <p className="text-md text-gray-500 italic">
                        Lancez une recherche pour afficher les r√©sultats.
                    </p>
                )}
            </div>
            
        </div>
    );
}